@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
* {
  box-sizing: border-box;
}
@font-face {
  font-family: 'Karla', sans-serif;
  src: url();
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-family: 'Karla', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  display: flex;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  text-align: center;
  overflow-y: hidden;
  overflow-x: hidden;
  
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.Message {
  height: 400px;
  width: 600px;
  border: 4px solid lightpink;
}

.Dialogs {
  width: 20%;
  height: 100%;
  border: 2px solid gray;
}

.work-tree {
  display: flex;
  justify-content: center;
  width: 80%;
  height: 100%;
  border: 2px solid red;
}

.dialog-window {
  width: 67%;
  height: 100%;
  border: 2px solid blue;
}

.talks {
  width: 33%;
  height: 100%;
  border: 2px solid violet;
}

.DialogList {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  height: 100%;
  overflow-y: auto;
}

.DialogList::-webkit-scrollbar{
  width: 0px;
}

.searchBox {
  padding: 5px;
  border-bottom: 2px solid black;
}

.dialog-item {
  display: flex;
  justify-content: flex-start;
  border: 2px solid lightslategray;
}

.avatar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
}

.dialog {
  height: 83%;
  border:4px solid lightyellow;
}

.txtArea {
  display: flex;
  justify-content: center;
  align-items: center;
}
.loginPage {
    font-size: 16px;
    background-color: #202124;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
}

.loginForm {
    background-color: #323233;
    border-radius: 15px;
    width: 80%;
    height: 550px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.loginPageAvatar {
    width: 400px;
    height: 450px;
    background-color: #202124;
}

.authForm {
    background-color: inherit;
    width: 400px;
    height: 350px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding-top: 5%;
    align-items: center;
    outline: none;
    box-shadow: 0 9px 50px hsla(20, 67%, 75%, 0.15);
    border-radius: 15px;
}

.authForm h2 {
    letter-spacing: 0.05em;
}

.regForm {
    padding-top: 2%;
    padding-bottom: 2%;
    height: -webkit-min-content;
    height: min-content;
}

input[class="form-input"]{
    width: 240px;
    height: 50px;
    margin-top: 2%;
    padding: 15px;
    font-size: 16px;
    font-family: 'Abel', sans-serif;
    color: #5E6472;
    background-color: #202124;
    outline: none;
    border: none;
    border-radius: 0px 5px 5px 0px;
    transition: 0.2s linear;
}

input:focus {
    -webkit-transform: translateX(-2px);
            transform: translateX(-2px);
    border-radius: 5px;
}

.buttons {
    display: flex;
    justify-content: space-between;
    padding: 5%;
    width: 60%;
}

.LoginPageButton {
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: #202124;
    
    color: #5E6472;
}

.LoginPageButton:hover {
    -webkit-transform: translatey(1px);
            transform: translatey(1px);
    box-shadow: none;
}

.LoginPageButton:hover {
-webkit-animation: ani 0.6s ease-in-out infinite alternate;
        animation: ani 0.6s ease-in-out infinite alternate;
}
@-webkit-keyframes ani {
    0% {
        -webkit-transform: translateY(1px);
                transform: translateY(1px);
    }
    100% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
}
@keyframes ani {
    0% {
        -webkit-transform: translateY(1px);
                transform: translateY(1px);
    }
    100% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
}

.LoginPageButton:disabled {
    -webkit-animation: none;
            animation: none;
    background-color: #202124;
}

.LoginPageButton:hover:disabled {
    -webkit-animation: none;
            animation: none;
    -webkit-transform: none;
            transform: none;
}

.loginPageAvatar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
}

.loginPageText {
    font-size: 18px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5%;
    color: #5E6472;
}

.ButtonText {
    background-color: #323233;
    font-weight: 700;
    padding: 15px;
    text-decoration: none;
}


